<template>
  <div class="singerSong">
    <div class="banner">
      <img :src="'https://y.gtimg.cn/music/photo_new/T001R150x150M000'+singer.Fsinger_mid+'.jpg?max_age=2592000'" alt=""/>
    </div>
   <div class="list">
      <ul>
        <li v-for="(item,index) in list">
          <p class="pleft" @click="play(index)">{{item.musicData.songname}}</p>
          <div class="pright"><i></i></div>
        </li>
      </ul>
    </div>
    <loading v-if="!list.length"></loading>
     <!--<div class="player" v-if="player">
      <div style="width: 3.75rem;height: 3.75rem;padding: .2143rem .535rem .2143rem .2143rem;display: inline-table;">
        <img :src="img" alt="" width="100%" style="border-radius: 5px;"/>
      </div>
      <div class="detail">
        <p>{{body}}</p>
        <p>{{name}}</p>
      </div>
      <div class="img">
        <img :src="isRun" alt="" @click="stopOrRun()"/>
        <img src="../assets/next_icon.png" alt="" @click="next()"/>
        <img src="../assets/download_icon2.png" alt=""/>
      </div>
      <audio :src="music" autoplay v-if="isRun=='static/pause_icon.png'"></audio>
    </div>-->
  </div>
</template>
<script>
  import loading from '../components/loading.vue'
  import {mapMutations} from 'vuex'
  import {mapGetters} from 'vuex'
  import {getSong} from '../jsons/recommend.js'
  import {ERR_OK} from '../jsons/config.js'
  export default{
    data(){
      return {
        list:[],
        musicId:0,
        body:"",
        name:"",
        img:"",
        music:"",
        isRun:"static/pause_icon.png",
        player:false
      }
    },
    computed:{
      ...mapGetters(['title','singer','isw'])
    },
    components:{loading},
    created(){
        document.body.scrollTop=0
        this.setIsWhite(false)
        this.setTitle(localStorage.title)
        this.selectedSinger(JSON.parse(localStorage.singerSong))
        this._getSong()
    },
    mounted() {
//      console.log(this.singer)
    },
    methods:{
      _getSong(){
        getSong(this.singer.Fsinger_mid).then((res) => {
          if(res.code===ERR_OK){
            this.list=res.data.list
          }
        })
      },
      play(index){
        /*this.name=this.songList[index].title.split("-")[0]
        this.body=this.songList[index].title.split("-")[1]
        this.img=this.songList[index].img
        this.music=this.songList[index].hash
        this.player=true
        this.musicId=index*/
      },
      stopOrRun(){
        this.isRun=this.isRun=="static/pause_icon.png"?"static/play_icon.png":"static/pause_icon.png"
      },
      next(){
        this.musicId++
        this.play(this.musicId)
      },
      ...mapMutations({
        selectedSinger:"SELECTED_SINGER",
        setTitle:"SET_TITLE",
        setIsWhite:"SET_ISWHITE"
      })
    },
    destroyed(){
      localStorage.title=localStorage.prevTitle
      this.setTitle(localStorage.prevTitle)
      this.setIsWhite(true)
      document.body.scrollTop=0
    }
  }
</script>
<style>
  .singerSong{
    position: absolute;
    top: -3rem;
    left: 0;
    width: 100%;
    background: #fff;
  }
  .banner{
    width: 100%;
    height: 12.5rem;
    position: relative;
    overflow: hidden;
  }
  .banner img{
    margin-top: -25px;
    width: 100%;
  }
  .list ul{
    padding-left: .7143rem;
    margin: 0;
  }
  .list li{
    width: 100%;
    height: 4.0714rem;
    border-bottom: 1px solid #e5e5e5;
    position: relative;
    display: table;
  }
  .pleft{
    width: 18.2rem;
    padding-right: .3571rem;
    display: table-cell;
    vertical-align: middle;
    padding-left: .3571rem;
    cursor: pointer;
    font-size: 1rem;
    box-sizing: border-box;
  }
  .list li:nth-child(1)>span{
    background: #e80000;
    color: #fff;
  }
  .list li:nth-child(2)>span{
    background: #ff7200;
    color: #fff;
  }
  .list li:nth-child(3)>span{
    background: #f8b300;
    color: #fff;
  }
  .pright{
    width: 2.5rem;
    height: 100%;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    display: table-cell;
  }
  .pright i{
    display: inline-block;
    width: 1.0357rem;
    height: 1.0357rem;
    background: url("../assets/download_icon.png");
    background-size: 100%;
    margin: 0 auto;
  }
  .player{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4.2143rem;
    background: rgba(0,0,0,.9);
    color: #fff;
  }
  .player p{
    margin: 0;
    -webkit-margin-before: 0em;
    -webkit-margin-after: 0em;
  }
  .detail{
    width: 8rem;
    margin-top: -3.5rem;
    display: inline-block;
    vertical-align: middle;
  }
  .detail p:nth-child(1){
    width: 100%;
    line-height: 1.7rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: .8571rem;
    color: #fff;
  }
  .detail p:nth-child(2){
    line-height: 1.3;
    font-size: .7143rem;
    color: #888;
  }
  .player .img{
    height: 1.786rem;
    display: inline-block;
  }
  .player .img img{
    height: 1.786rem;
    width: 1.786rem;
    vertical-align: middle;
    margin-top: -3rem;
    margin-right: 0.5rem;
  }
</style>



